/* Search Bar Component Styles */

:host {
  display: block;
  width: 100%;
}

/* Width utilities */
.w-4\/5 {
  width: 80%;
}

/* Enhanced search container */
.focus-within\:ring-2:focus-within {
  box-shadow: 0 0 0 2px rgba(20, 120, 133, 0.1);
}

/* Input focus effects */
input:focus {
  outline: none;
}

/* Button hover effects */
.hover\:bg-blue-600:hover {
  background-color: #0F5F66;
}

/* Brand colors */
.text-brand {
  color: #147885;
}

.hover\:text-brand:hover {
  color: #147885;
}

.bg-brand {
  background-color: #147885;
}

/* Enhanced styling for search bar */
.rounded-r-lg {
  border-top-right-radius: 0.5rem;
  border-bottom-right-radius: 0.5rem;
}

.border-l {
  border-left-width: 1px;
}

/* Border styling */
.border-stroke-weak {
  border-color: #ADB4CC;
}

.border-t {
  border-top-width: 1px;
}

.border-none {
  border: none;
}

/* Text decoration */
.underline {
  text-decoration: underline;
}

/* Layout utilities */
.justify-between {
  justify-content: space-between;
}

/* Spacing utilities */
.space-x-2 > * + * {
  margin-left: 0.5rem;
}

.space-x-3 > * + * {
  margin-left: 0.75rem;
}

/* Remix Icons styling */
.ri-filter-line {
  font-size: 1.125rem; /* 18px */
}

/* Secondary button styling */
.border-0 {
  border: none !important;
  outline: none !important;
}

.border-0:focus {
  outline: none !important;
  border: none !important;
}

/* Dropdown styling */
.z-50 {
  z-index: 50;
}

.shadow-lg {
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
}

.hover\:bg-gray-50:hover {
  background-color: #f9fafb;
}

/* Radio button styling */
input[type="radio"] {
  accent-color: #147885;
}

/* Disabled button styling */
.cursor-not-allowed {
  cursor: not-allowed;
}

/* Additional Remix icons */
.ri-sparkling-fill,
.ri-user-line,
.ri-flow-chart,
.ri-arrow-down-s-line,
.ri-arrow-right-long-line,
.ri-close-line {
  font-size: 1.125rem;
}

/* Remove focus ring from search container */
.focus-within\:ring-2:focus-within {
  box-shadow: none;
}

/* Remove input focus styles completely */
input:focus {
  outline: none !important;
  border: none !important;
  box-shadow: none !important;
}

/* Placeholder opacity */
.placeholder-opacity-30::placeholder {
  opacity: 0.3;
}

/* Button dimensions */
.w-9 {
  width: 2.25rem; /* 36px */
}

.h-9 {
  height: 2.25rem; /* 36px */
}

/* Light border for dropdown */
.border-gray-200 {
  border-color: #e5e7eb;
}

/* Text styling for arrow icon */
.text-lg {
  font-size: 1.125rem;
  line-height: 1.75rem;
}

.text-xl {
  font-size: 1.25rem;
  line-height: 1.75rem;
}

/* Hover effects */
.hover\:bg-gray-50:hover {
  background-color: #f9fafb;
}

.hover\:bg-gray-100:hover {
  background-color: #f3f4f6;
}

.hover\:bg-teal-700:hover {
  background-color: #0f766e;
}

/* Panel styling */
.bg-opacity-50 {
  background-opacity: 0.5;
}

.bg-opacity-10 {
  background-opacity: 0.1;
}

.shadow-xl {
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
}

.transform {
  transform: translateX(var(--tw-translate-x)) translateY(var(--tw-translate-y)) rotate(var(--tw-rotate)) skewX(var(--tw-skew-x)) skewY(var(--tw-skew-y)) scaleX(var(--tw-scale-x)) scaleY(var(--tw-scale-y));
}

.transition-all {
  transition-property: all;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 150ms;
}

.transition-transform {
  transition-property: transform;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 150ms;
}

.transition-opacity {
  transition-property: opacity;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 150ms;
}

.duration-300 {
  transition-duration: 300ms;
}

.ease-in-out {
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}

.translate-x-0 {
  --tw-translate-x: 0px;
  transform: translateX(var(--tw-translate-x)) translateY(var(--tw-translate-y)) rotate(var(--tw-rotate)) skewX(var(--tw-skew-x)) skewY(var(--tw-skew-y)) scaleX(var(--tw-scale-x)) scaleY(var(--tw-scale-y));
}

.translate-x-full {
  --tw-translate-x: 100%;
  transform: translateX(var(--tw-translate-x)) translateY(var(--tw-translate-y)) rotate(var(--tw-rotate)) skewX(var(--tw-skew-x)) skewY(var(--tw-skew-y)) scaleX(var(--tw-scale-x)) scaleY(var(--tw-scale-y));
}

/* Fixed positioning */
.fixed {
  position: fixed;
}

.absolute {
  position: absolute;
}

.inset-0 {
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.right-0 {
  right: 0;
}

.top-0 {
  top: 0;
}

.bottom-0 {
  bottom: 0;
}

.left-0 {
  left: 0;
}

/* Panel dimensions */
.w-80 {
  width: 20rem;
}

.h-full {
  height: 100%;
}

/* Margins and spacing */
.ml-3 {
  margin-left: 0.75rem;
}

.space-y-6 > * + * {
  margin-top: 1.5rem;
}

.space-y-3 > * + * {
  margin-top: 0.75rem;
}

.mb-3 {
  margin-bottom: 0.75rem;
}

/* Colors */
.text-gray-900 {
  color: fffff;
}

.text-gray-700 {
  color: #374151;
}

.text-gray-500 {
  color: #6b7280;
}

.border-gray-200 {
  border-color: #e5e7eb;
}

.border-gray-300 {
  border-color: #d1d5db;
}

.bg-black {
  background-color: #000000;
}

.bg-gray-900 {
  background-color: fffff;
}

/* Focus ring for brand color */
.focus\:ring-brand:focus {
  box-shadow: 0 0 0 2px rgba(20, 120, 133, 0.2);
}

/* Checkbox styling */
input[type="checkbox"] {
  accent-color: #147885;
}

/* Select styling */
select {
  appearance: none;
  background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' fill='none' viewBox='0 0 20 20'%3e%3cpath stroke='%236b7280' stroke-linecap='round' stroke-linejoin='round' stroke-width='1.5' d='M6 8l4 4 4-4'/%3e%3c/svg%3e");
  background-position: right 0.5rem center;
  background-repeat: no-repeat;
  background-size: 1.5em 1.5em;
  padding-right: 2.5rem;
}

/* Text sizing */
.text-lg {
  font-size: 1.125rem;
  line-height: 1.75rem;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .w-4\/5 {
    width: 95%;
    padding: 0 1rem;
  }
  
  .flex {
    flex-direction: column;
  }
  
  .border-l {
    border-left: none;
    border-top: 1px solid;
  }
  
  .rounded-r-lg {
    border-radius: 0.5rem;
    margin-top: 0.5rem;
    width: 100%;
  }
}
